.bitqueditor {
  height: 100%;
  width: 100%;
  display: flex;
  flex-direction: column;
  position: relative;
}
[contenteditable="true"] {
  user-select: text;
}
.Appb {
  height: 100%;
  width: 100%;
  display: flex;
  margin: 0 0 -11px 0;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}
.widget {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  padding: 0 0 20px 0;
  min-height: 240px;
}
.widget-display{
  position: relative;
  display: flex;
  flex: 1 1;
  width: 100%;
  height: 100%;
  overflow: auto;
}
.widget-editor {
  display: flex;
  flex-flow: column wrap;
  overflow: auto;
  width: 100%;
  padding: 10px 10px 0;
}
.widget-option {
  margin-right: 20px;
  width: 150px;
}
.widget .widget-option {
  align-self: center;
}
.widget .widget-editor .widget-option {
  align-self: flex-start;
}

.workspace__wrapper {
  position: relative;
  display: flex;
  flex: 1 1 0%;
  flex-direction: column;
  height: 100%;
  width: 100%;
  overflow: hidden;
  border-right: 1px solid #ced4da;
}

.editor__wrapper {
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  overflow: hidden;
}

.query-editor{
  display: flex;
}
.variable-editor {
  display: flex;  
  flex: 1 1;
}
.variable-editor>.CodeMirror {
  height: auto !important;
}
.handle {
  flex: 0 0 auto;
}
.CodeMirror {
  height: 100%;
  left: 0;
  position: absolute;
  top: 0;
  width: 100%;
}
.handle {
  background: #f7f7f7;
  width: 100%;
  height: 20px;
  position: relative;
  user-select: none;
  cursor: row-resize;
  border-top: 1px solid #ddd;
  border-bottom: 1px solid #ddd;
  font-size: 12px;
  font-feature-settings: "c2sc";
  font-variant: small-caps;
  font-weight: 700;
  letter-spacing: 1px;
  line-height: 14px;
  padding-left: 10px;
}

.handle:before {
  content: '\2261'; /* https://en.wikipedia.org/wiki/Triple_bar */
  color: #999;
  position: absolute;
  left: 50%;
  top: -2px;
}

.handle:hover {
  background: #f0f0f0;
}

.handle:hover:before {
  color: #000;
}
.sizeChanger {
	background-color: #f3f3f3;
	width: .5em;
	cursor: col-resize;
}
.execute-button {
  position: absolute;
  border-radius: 50%;
  width: 50px;
  height: 50px;
  line-height: 12px;
  font-size: 12px;
  z-index: 666;
  top:50%;
  left: calc(50% - 25px);
  border: none;
  outline: none !important;
  background: #c7c7c7 url('../../assets/images/play.svg') no-repeat center;
  background-size: 25px 25px;
  background-position-x: 60%;
}
.execute-button:active {
  -webkit-box-shadow: rgb(0, 0, 0) 4px 4px 15px 0px inset, 5px 5px 15px 5px rgba(0,0,0,0); 
  box-shadow: rgb(0, 0, 0) 4px 4px 15px 0px inset, 5px 5px 15px 5px rgba(0,0,0,0);
}

.execute-button:hover {
  cursor: pointer;
}

div.CodeMirror span.CodeMirror-matchingbracket {
  color: #555 !important
}

.bitqueditor__toolbar {
  align-items: center;
  border-bottom: 1px solid #d0d0d0;
  cursor: default;
  display: flex;
  flex-direction: row;
  min-height: 54px;
  padding: 7px 14px 6px;
  -webkit-user-select: none;
  -ms-user-select: none;
  user-select: none;
}
.over-wrapper {
  position: relative;
  display: flex;
  // flex: 1 1 100%;
  height: calc(100% - 54px);
}
.topBarWrap {
  display: flex;
}
.topBar {
  align-items: center;
  border-bottom: 1px solid #d0d0d0;
  cursor: default;
  display: flex;
  flex-direction: row;
  flex: 1 1;
  height: 54px;
  overflow-y: visible;
  padding: 7px 14px 6px;
  -webkit-user-select: none;
  -ms-user-select: none;
  user-select: none;
  &__logo {
    height: 40px;
    margin: 0 20px 0 0;
    &__img {
      height: 100%;
    }
  }
  &__button {
    background: linear-gradient(#f9f9f9,#ececec);
    border: 0;
    border-radius: 3px;
    box-shadow: inset 0 0 0 1px rgba(0,0,0,.2), 0 1px 0 hsla(0,0%,100%,.7), inset 0 1px #fff;
    color: #555;
    cursor: pointer;
    display: inline-block;
    margin: 0 5px;
    padding: 3px 11px 5px;
    text-decoration: none;
    text-overflow: ellipsis;
    white-space: nowrap;
    max-width: 150px;
    &:disabled {
      opacity: 0.5;
      cursor: default;
    }
  }
  .endpointURL {
    border: none;
    outline: none;
    font-size: 14px;
    flex: 1 1 auto
  }
}